@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@400;500&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Fira Sans',sans-serif;
  -webkit-font-smoothing: antialiased;
  font-weight: 400;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3 {
  font-weight: 500;
}

.form-block {
  padding: 10px;
}

.view-mode  .ant-form-item-extra {
  display: none;
}

.ant-radio-button-wrapper {
  margin-right: 5px;
  border-radius: 5px;
}

.ant-radio-button-wrapper:last-child {
  border-radius: 5px;
}


@media (max-width: 576px) {
  .view-mode {
    display: none;
  }
}

p {
  margin: 0;
  padding: 0;
}
.card-radio-wrapper {
    width: 45%;
    margin-right: 5px;
    border: 1px solid #e4e4e4;
    border-radius: 10px;
    padding: 10px;
}

.card-radio-description {
    color: #00000080;
}

.ant-radio-wrapper-checked.card-radio-wrapper {
    border: 1px solid #48B5FE;
}
.color-radio,
.color-radio + .ant-radio-button-wrapper:last-child,
.color-radio + .ant-radio-button-wrapper:first-child {
    border-radius: 100% !important;
    margin-right: 15px;
    border-left: transparent i !important;
}

.color-radio + .ant-radio-button-wrapper:not(:first-child)::before {
    display: none;
}

.space-block {
    margin: 8px 4px 0;
    padding: 4px 4px 0;
}
